import logo from './logo.svg';
import './App.css';
import { useEffect, useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText,
} from 'reactstrap';
import Navigation from './component/Navigation';
import Languages from "./Languages";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Home from './component/Home';
import Pulse from './component/images/pulse.svg';
import $ from 'jquery';
import Downloads from './component/Downloads';

function App() {

  const [language, setLanguage] = useState(localStorage.getItem("language") || "eng");
  const [languages, setLanguages] = useState(Languages);
  const [loggedIn, setLoggedIn] = useState(localStorage.getItem("loggedIn") || false);
  const [username, setUsername] = useState("admin");
  const [password, setPassword] = useState("password");
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState(false);
  const [fadeInLogin, setFadeInLogin] = useState(false);

  function login() {
    if (username == "admin" && password == "password") {
      setLoading(true)
      setTimeout(() => {
        $(".login-absoluter").animate({ opacity: 0 }, 400, () => {
          setLoggedIn(true)
          setLoading(false)
        })
      }, 2000)
    }
    else {
      setErrors(true)
    }
  }

  function logout() {
    setLoggedIn(false)
    setFadeInLogin(true)
  }

  useEffect(() => { localStorage.setItem("language", language) }, [language])
  useEffect(() => {
    if (loggedIn) {
      setErrors(false)
      localStorage.setItem("loggedIn", loggedIn)
    }
    else {
      localStorage.removeItem("loggedIn")
    }
  }, [loggedIn])

  useEffect(() => {
    if (fadeInLogin) {
      $(".login-absoluter").animate({ opacity: 0 }, 0, () => {
        $(".login-absoluter").animate({ opacity: 1 }, 400, () => {
          setFadeInLogin(false)
        })
      })
    }
  }, [fadeInLogin])

  return (
    <>
      {!loggedIn && <div className='login-absoluter'>
        {loading && <img src={Pulse}></img>}
        {!loading && <div>
          {errors && <p className='bg-danger text-white rounded p-2'>Invalid credentials</p>}
          <input type="text" value={username} onChange={(e) => setUsername(e.target.value)}></input>
          <input type="text" value={password} onChange={(e) => setPassword(e.target.value)}></input>
          <button className='btn btn-primary' onClick={(e) => { e.preventDefault(); login() }}>Submit</button>
        </div>}
      </div>}
      <BrowserRouter>
        <Navigation logout={logout} setLanguage={setLanguage} language={languages[language]} />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/downloads" element={<Downloads />} />
        </Routes>
      </BrowserRouter>
    </>
  );
}

export default App;
