import { useEffect, useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText,
} from 'reactstrap';
import German from './images/german.png';
import English from './images/english.png';
import Spanish from './images/spanish.png';
import { Outlet, Link } from 'react-router-dom';

function Navigation(props) {

  const languages = [
    { language: "eng", imgPath: English },
    { language: "spa", imgPath: Spanish },
    { language: "ger", imgPath: German }
  ]

  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <div className="container-fluid">
        <a className="navbar-brand" href="#">Navbar</a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon" />
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0 w-100">
            <li className="nav-item">
              <Link className="nav-link active" to="/">{props.language.nav.home}</Link>
            </li>
            <li className="nav-item">
            <Link className="nav-link active" to="/downloads">{props.language.nav.downloads}</Link>
            </li>
            <li className="nav-item dropdown">
              <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                {props.language.nav.dropdown1.name}
              </a>
              <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                <li><a className="dropdown-item" href="#">{props.language.nav.dropdown1.items[0]}</a></li>
                <li><a className="dropdown-item" href="#">{props.language.nav.dropdown1.items[1]}</a></li>
                <li><hr className="dropdown-divider" /></li>
                <li><a className="dropdown-item" href="#">{props.language.nav.dropdown1.items[2]}</a></li>
              </ul>
            </li>
            <li className="nav-item">
              <a className="nav-link disabled" href="#" tabIndex={-1} aria-disabled="true">Disabled</a>
            </li>
            <form className="d-flex">
              <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
              <button className="btn btn-outline-success" type="submit">Search</button>
            </form>
            <li className="nav-item dropdown ms-auto position-relative">
              <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                <img className='language-img' src={languages.find(l => l.language == props.language.language).imgPath}></img>
              </a>
              <ul className="dropdown-menu languages dropdown-menu-end" aria-labelledby="navbarDropdown">
                {props.language.language != "eng" ? <li className='eng'><a className="dropdown-item" href="#" onClick={() => props.setLanguage("eng")}><img src={English}></img> English</a></li> : null}
                {props.language.language != "spa" ? <li className='spa'><a className="dropdown-item" href="#" onClick={() => props.setLanguage("spa")}><img src={Spanish}></img> Spanish</a></li> : null}
                {props.language.language != "ger" ? <li className='ger'><a className="dropdown-item" href="#" onClick={() => props.setLanguage("ger")}><img src={German}></img> German</a></li> : null}
              </ul>
            </li>
            <li className="nav-item">
              <a className="nav-link" href="#" tabIndex={-1} onClick={() => props.logout()}>{props.language.nav.logout}</a>
            </li>
          </ul>
        </div>
      </div>
    </nav >
  );
}

export default Navigation;
