import { useState } from 'react';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

function Home(props) {

  const [todos, setTodos] = useState([])
  const [task, setTask] = useState("")

  function addTask() {
    setTodos(oldTodos => [...oldTodos, { id: Date.now(), name: task }])
    setTask("")
  }

  function deleteTask(id) {
    setTodos(oldTodos => oldTodos.filter((oldTask) => oldTask.id != id))
  }

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };

  return (
    <>
      <div>
        <input type="text" value={task} onChange={e => setTask(e.target.value)}></input>
        <button onClick={addTask} disabled={task == ""}>Add Task</button>
        <ul className='todo'>
          {todos.map((task) => <li key={task.id}>{task.name} <a href='javascript:void(0)' onClick={() => deleteTask(task.id)} className='bg-danger text-white'>x</a></li>)}
        </ul>
      </div>
      <div className='row'>
        <div className='col-md-4'>
          <Slider {...settings}>
            <div>
              <h3>1</h3>
            </div>
            <div>
              <h3>2</h3>
            </div>
            <div>
              <h3>3</h3>
            </div>
            <div>
              <h3>4</h3>
            </div>
            <div>
              <h3>5</h3>
            </div>
            <div>
              <h3>6</h3>
            </div>
          </Slider>
        </div>
        <div className='col-md-4'>
          <Slider {...settings}>
            <div>
              <h3>1</h3>
            </div>
            <div>
              <h3>2</h3>
            </div>
            <div>
              <h3>3</h3>
            </div>
            <div>
              <h3>4</h3>
            </div>
            <div>
              <h3>5</h3>
            </div>
            <div>
              <h3>6</h3>
            </div>
          </Slider>
        </div>
        <div className='col-md-4'>
          <Slider {...settings}>
            <div>
              <h3>1</h3>
            </div>
            <div>
              <h3>2</h3>
            </div>
            <div>
              <h3>3</h3>
            </div>
            <div>
              <h3>4</h3>
            </div>
            <div>
              <h3>5</h3>
            </div>
            <div>
              <h3>6</h3>
            </div>
          </Slider>
        </div>
      </div>
    </>
  );
}

export default Home;
